
const { Image, Layer, Line, Circle } = KV;


export default class PaintingLayer extends Component{
    constructor(props){
        super(props);

    }

    render(){


        let {points, fill, closed, id, curtTag, fixFirstSpotHit} = this.props;

        let spots = null;

        points.forEach((elt, i)=>{

            if(!spots) { spots = [] };

            if(i%2 !== 0) return;

            let x = points[i],
                y = points[i+1];

            let circle = (
                <Circle
                    key={i}
                    onMouseOver={
                        ev=> {
                            return i===0? fixFirstSpotHit(true,curtTag) : null
                        }
                    }
                    onMouseOut={
                        ev=> i===0? fixFirstSpotHit(false, curtTag) : null
                    }

                    {...{
                        x,
                        y,
                        radius: 4,
                        fill: 'grey'
                    }}
                />
            );

            spots.push(circle);
        });

        return (
            <Layer
            >
                <Line
                    {...{
                        points,
                        stroke: 'blue',
                        strokeWidth: 2,
                        lineJoin: 'round',
                        closed,
                        fill
                    }}
                />
                {spots}
            </Layer>
        );
    }
}



