import S from './style.scss';
import {connect} from 'react-redux';
let {Stage, Layer, Circle, Line, Image} = KV;
import {
    fitStageIntoParentContainer,
    drawImgCenter,
    getRectToImg
} from 'util/KonvaUtil';
import PaintingLayer from './PaintingLayer';
import * as actionCreators from './BoardRedux';

export default class PaintingGroup extends Component{
    constructor(props){
        super(props);

        this.state = {
            image: null
        }

        this.stageWidth = 760;
        this.stageHeight = 500;



        this.calcParm = {};

        this.getPointerPosition = this.getPointerPosition.bind(this);
        this.mark = this.mark.bind(this);
        this.addSpot = this.addSpot.bind(this);
        this.closeLine = this.closeLine.bind(this);
        this.fixFirstSpotHit = this.fixFirstSpotHit.bind(this);
    }

    getPointerPosition(){

        return this.refs.stage.getStage().getPointerPosition();
    }
    //  layer 
    addSpot(x, y){
        let {layers, whichLayer} = this.state;

        let {disW, disT} = getRectToImg(x, y, this.calcParm);

        layers = layers.map(elt=>{
            if(whichLayer===elt.id){
                elt.points = [...elt.points, x, y];
                elt.actualPoints = [...elt.actualPoints, disW, disT];
            }
            return elt;
        });

        this.setState({
            layers
        });
    }

    closeLine(){
        let {layers, whichLayer} = this.state;
        layers = layers.map(elt=>{
            if(whichLayer===elt.id){
                elt.closed = true;
            }
            return elt;
        });

        this.setState({
            layers
        });
    }

    fixFirstSpotHit(flag){
        let {layers, whichLayer} = this.state;
        layers = layers.map(elt=>{
            if(whichLayer===elt.id){
                elt.firstSpotHit = flag;
            }
            return elt;
        });

        this.setState({
            layers
        });
    }

    // end  layer 

    mark(){

        let {layersData, curtTag} = this.props;
        if(!curtTag) return;

        let {addSpot, closeLine} = this.props;

        let layer = layersData[curtTag];




        if(layer.closed) return;

        if(layer.firstSpotHit){
            closeLine(curtTag);

        }else{

            let {x,y} = this.getPointerPosition();
            addSpot(x,y, this.calcParm, curtTag);
        }

    }
    markBack(){

        let {layersData, layers} = this.state;

        let layer = layers[whichLayer];



        if(layer.closed) return;

        if(layer.firstSpotHit){
            this.closeLine();

        }else{

            let {x,y} = this.getPointerPosition();
            this.addSpot(x,y)
        }

        console.log(layer.actualPoints);

    }

    componentWillReceiveProps(nextProps){
        // console.log(nextProps);
        // this.imgObj.src = nextProps.photoData.src;
        // let {url} = nextProps.photoData;
        // let imgObj = new window.Image();
        //
        // imgObj.onload = ()=>{
        //     this.setState({
        //         image: imgObj
        //     })
        // }
        //
        // imgObj.src = url;
    }

    componentDidMount(){
        // let {stageContainer, stage} = this.refs;
        //
        // let {offsetWidth, offsetHeight} = stageContainer;
        //
        // let {stageWidth, stageHeight} = this;
        //
        // let fixStage = this.fixStage = () => fitStageIntoParentContainer(stageContainer, stageWidth, stageHeight, stage);

        // fixStage();

        // window.addEventListener('resize', fixStage);

        // 

        // imgObj.src = url;

    }

    componentWillUnmount(){
        // window.removeEventListener("resize", this.fixStage);
    }

    render(){

        // console.log(this.props);
        let { layersData, image, addSpot, closeLine, fixFirstSpotHit, curtTag} = this.props;

        let layerComp = null;

        console.log(this.props, 'curtTag');

        if(curtTag){
            let layer = layersData[curtTag];

            let {points, fill, closed, id} = layer;

            layerComp = (
                <PaintingLayer
                    {...{
                        points,
                        fill: fill,
                        closed,
                        id,
                        fixFirstSpotHit,
                        curtTag
                    }}

                />
            );
        }

        let {stageWidth, stageHeight} = this;

        let bodyInfo = {x: 0, y: 0, width: 0, height: 0};

        if(image){

            let {width: imgWidth, height: imgHeight} = image;

            let {x, y, w, h, scale} = drawImgCenter(imgWidth, imgHeight, stageWidth, stageHeight);
            bodyInfo = {x,y,width: w, height: h};

            this.calcParm = {x, y, scale};

        }


        return (
            <div
                ref="stageContainer"
                className="stageContainer"
                style={{width: '100%', height: '100%'}}
            >
                <Stage
                    onClick={this.mark}
                    ref="stage"
                    {...{
                        width: stageWidth,
                        height: stageHeight
                    }}
                >
                    <Layer>
                        <Image
                            {...bodyInfo}
                            {...{
                                image,
                                stroke: 'red',
                                strokeWidth: 4
                            }}
                        />
                    </Layer>
                    {layerComp}
                </Stage>


            </div>
        );
    }
}



